body {
  margin: 0;
  padding: 0;
  font-family: "pen Sans", sans-serif;
  background-color: #7f8c8d;
  height: 100%;
}

html,
body,
#root,
.container {
  height: 100%;
  display: flex;
  justify-content: center;
}

.loader {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.movies {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.movies .movie {
  width: 45%;
  background-color: white;
  margin-bottom: 70px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.movie img {
  max-width: 120px;
}

.movie h3,
.movie h5 {
  font-weight: 300;
}
